tion: row;\n  padding: 6px 8px 6px 8px;\n  width: auto;\n  color: #403F3F;\n  gap: 6px;\n  font-family: $default-font;\n  font-size: 14px;\n  justify-content: space-between;\n  align-items: center;\n  margin: 0 6px 0 12px;\n  background: transparent;\n  &:hover {\n    color: #1073BC;\n    cursor: pointer;\n}\n  \n  border: none;    \n     span:last-child {\n        display: none;        \n        align-items: center;\n        flex-direction: row;\n        padding: 2px 1px;\n        vertical-align: middle;\n        font-weight: 500;\n        @include breakpoint(laptop) {\n            display: inline-block;\n        }    \n    }\n    span:first-child {        \n            svg {\n                height: 24px;\n                width: 24px;                              \n                vertical-align: middle;                \n            \n           }\n           \n        }\n       \n}\n.menu-wrapper {\n    font-size: 14px;\n    font-family: $default-font;\n    position: relative;        \n    @include transition(all .3s ease-in-out);\n    \n   \n    > div {\n        max-height: 0;\n        overflow: hidden;\n        @include transition(all 0.3s ease-in-out);\n        position: absolute;\n        display: flex;\n        flex-direction: column;\n        background-color: white;             \n        box-shadow: 0px 1px 3px 0px #0000001A;\n        width: 375px;\n        z-index: 100;\n        top: 10px;\n        right: -85px;          \n        > div:first-child > span {\n            display: block;\n            &:last-child {\n             font-weight: bolder;\n            }\n        } \n        ul {\n            list-style-type: none;\n            li {\n                margin: 12px 0;\n                a, a:link, a:visited {\n                    color: #1A589F;\n                }\n            }\n        }\n        \n        > *:nth-child(odd)  {\n            background-color: #F9F9F9;\n            padding: 32px;            \n            display: flex\n        ;\n     